.cart-item {
  width: 100%;
  height: 100px;
  padding: 8px;
  display: flex;
  background-color: #4285f442;

  &.with-offset {
    margin-top: 12px;
  }

  .image {
    width: 84px;
    object-fit: scale-down;
    background-color: white;
    border: 1px solid black;
  }

  .item-details {
    width: calc(100% - 84px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 15px;
    font-size: 14px;

    .price {
      font-weight: bold;
      align-self: flex-end;
      padding-right: 3px;
    }
  }
}
